</template>

<script>
import SidebarLeft from '@/components/SidebarLeft.vue'

export default {
  components: { SidebarLeft },
  name: 'UserShow',
  data() {
    return { username: localStorage.getItem('name') }
  },
  mounted() {},
  methods: {
    handleCommand(command) {
      if (command === 'exit') {
        this.$router.push({ name: 'SignUp' })
      }
    },
  },
}
</script>

