<template>
  <div>
    <el-button size="mini" type="danger" @click="handleAdd(1)">教师添加</el-button>
    <el-table :data="studentList" style="width: 100%" border>
      <el-table-column prop="code" label="工号"></el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="sex" label="性别"></el-table-column>
      <el-table-column prop="phone" label="电话"></el-table-column>
      <el-table-column prop="department" label="系部"></el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleList(scope.$index, scope.row)">List</el-button>
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row, 2)">Edit</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">
            Delete
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="教师信息修改"
      :visible.sync="dialogVisible"
      width="30%"
      :showClose="false"
      top="60px"
    >
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="工号">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-select v-model="form.sex" placeholder="请选择性别">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="系部">
          <el-input v-model="form.department"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="okBtn()">确 定</el-button>
      </span>
    </el-dialog>
    <div class="flex-table">
      <el-table :data="studentCourseList" style="width: 40%" border class="table2">
        <el-table-column prop="studentCode" label="学号"></el-table-column>
        <el-table-column prop="studentName" label="姓名"></el-table-column>
        <el-table-column prop="courseName" label="课程名"></el-table-column>
        <el-table-column prop="achievement" label="成绩"></el-table-column>
      </el-table>

      <el-table :data="minMaxAvgList" style="width: 40%" border>
        <el-table-column prop="maxAchievement" label="最高分"></el-table-column>
        <el-table-column prop="minAchievement" label="最低分"></el-table-column>
        <el-table-column prop="avgAchievement" label="平均分"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'TeacherCRUD',
  data() {
    return {
      studentList: [],
      studentCourseList: [],
      minMaxAvgList: [],
      dialogVisible: false,
      current: 0,
      form: {
        id: '',
        code: '',
        name: '',
        sex: '',
        phone: '',
        department: '',
      },
      index: 0,
    }
  },
  methods: {
    handleAdd(index) {
      this.dialogVisible = true
      this.index = index
    },
    handleList(_, row) {
      axios
        .post(`/scms/teacher/select-student-course`, row)
        .then(res => {
          this.studentCourseList = res.data
        })
        .catch(err => {
          console.error(err)
        })
      axios
        .post(`/scms/teacher/select-min-max-avg`, row)
        .then(res => {
          this.minMaxAvgList = res.data
        })
        .catch(err => {
          console.error(err)
        })
    },
    handleEdit(_, row, index) {
      this.dialogVisible = true
      this.form.id = row.id
      this.form = row
      this.index = index
    },
    handleDelete(_, row) {
      axios
        .get(`/scms/teacher/delete?id=${row.id}`)
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
          this.getStudent()
        })
        .catch(err => {
          console.error(err)
        })
    },
    okBtn() {
      this.dialogVisible = false
      if (this.index === 1) {
        axios
          .post(`/scms/teacher/add`, this.form)
          .then(res => {
            this.$message({
              message: res.data,
              type: 'success',
            })
            this.getStudent()
          })
          .catch(err => {
            console.error(err)
          })
      } else if (this.index === 2) {
        axios
          .post(`/scms/teacher/update`, this.form)
          .then(res => {
            this.$message({
              message: res.data,
              type: 'success',
            })
            this.getStudent()
          })
          .catch(err => {
            console.error(err)
          })
      }
    },
    getStudent() {
      axios
        .get(`/scms/teacher/list?page=${this.current}`)
        .then(res => {
          this.studentList = res.data.content
        })
        .catch(err => {
          console.error(err)
        })
    },
  },
  mounted() {
    this.getStudent()
  },
}
</script>
<style>
.el-main[data-v-344eecd0] {
  line-height: 0px;
}
</style>
<style scoped>
.table2 {
  margin-right: 15px;
}
.flex-table {
  display: flex;
  margin-top: 50px;
}
</style>
