<template>
  <div>
    <el-button size="mini" type="danger" @click="handleAdd(1)">课程增加</el-button>
    <el-table :data="studentList" style="width: 100%" border>
      <el-table-column prop="code" label="课程编号"></el-table-column>
      <el-table-column prop="name" label="课程名称"></el-table-column>
      <el-table-column prop="credit" label="学分"></el-table-column>
      <el-table-column prop="teacherId" label="教师Id"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="{}">
          <el-input
            v-model="searchCode"
            size="mini"
            placeholder="根据课程名模糊查询"
            @keydown.native.enter="searchCodeMethod"
          />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row, 2)">Edit</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">
            Delete
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="课程信息修改"
      :visible.sync="dialogVisible"
      width="30%"
      :showClose="false"
      top="60px"
    >
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="课程编号">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="课程名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="学分">
          <el-input v-model="form.credit"></el-input>
        </el-form-item>
        <el-form-item label="教师Id">
          <el-input v-model="form.teacherId"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="okBtn()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'CourseCRUD',
  data() {
    return {
      studentList: [],
      dialogVisible: false,
      searchId: '',
      searchCode: '',
      form: {
        id: '',
        code: '',
        name: '',
        credit: '',
        teacherId: '',
      },
      index: 0,
    }
  },
  methods: {
    handleAdd(index) {
      this.dialogVisible = true
      this.index = index
    },
    handleEdit(_, row, index) {
      this.dialogVisible = true
      this.form.id = row.id
      this.form = row
      this.index = index
    },
    handleDelete(_, row) {
      axios
        .get(`/scms/course/delete?id=${row.id}`)
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
          this.getCourse()
        })
        .catch(err => {
          console.error(err)
        })
    },
    okBtn() {
      this.dialogVisible = false
      if (this.index === 1) {
        axios
          .post(`/scms/course/add`, this.form)
          .then(res => {
            this.$message({
              message: res.data,
              type: 'success',
            })
            this.getCourse()
          })
          .catch(err => {
            console.error(err)
          })
      } else if (this.index === 2) {
        axios
          .post(`/scms/course/update`, this.form)
          .then(res => {
            this.$message({
              message: res.data,
              type: 'success',
            })
            this.getCourse()
          })
          .catch(err => {
            console.error(err)
          })
      }
    },
    getCourse() {
      axios
        .get(`/scms/course/list?page=${0}`)
        .then(res => {
          this.studentList = res.data.content
        })
        .catch(err => {
          console.error(err)
        })
    },
    searchCodeMethod() {
      if (this.searchCode === '') {
        this.getCourse()
        return
      }
      axios
        .get(`/scms/course/list-name?page=${0}&searchVal=${this.searchCode}`)
        .then(res => {
          this.studentList = res.data.content
          this.$message({
            message: 'search successfully',
            type: 'success',
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
  },
  mounted() {
    this.getCourse()
  },
}
</script>

<style>
.el-main[data-v-344eecd0] {
  line-height: 0px;
}
</style>

