<template>
  <div>
    <div class="login-box">
      <div class="login">
        <el-form :model="user" label-width="80px" size="mini">
          <el-form-item label="身份">
            <el-select v-model="user.type" placeholder="请选择身份">
              <el-option label="学生" value="1"></el-option>
              <el-option label="教师" value="2"></el-option>
              <el-option label="管理员" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="用户">
            <el-input v-model="user.username"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="user.password" show-password></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">注册</el-button>
            <el-button type="primary" @click="login">登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SignUp',
  data() {
    return {
      user: {
        type: '',
        username: '',
        password: '',
      },
    }
  },
  methods: {
    login() {
      // 验证登录请求
      switch (this.user.type) {
        case '1':
          axios
            .get(
              `/scms/student/login-name-password?name=${this.user.username}&password=${this.user.password}`
            )
            .then(res => {
              if (res.data != 'Login Failed') {
                // 持久化数据
                localStorage.setItem('name', this.user.username)
                this.$router.push({ name: 'UserShow' })
              } else {
                this.$message({
                  showClose: true,
                  message: res.data,
                  type: 'error',
                })
              }
            })
            .catch(err => {
              console.error(err)
            })
          break
        case '2':
        case '3':
          this.$message({
            showClose: true,
            message: '该功能在开发中！请尝试学生账号登录···',
            type: 'error',
          })
          break
        default:
          this.$message({
            showClose: true,
            message: '请选择你的身份！',
            type: 'error',
          })
          break
      }
    },
  },
}
</script>

<style scoped>
.login-box {
  display: flex;
  justify-content: center;
  margin-top: 220px;
}
.login {
  width: 270px;
}
</style>
