<template>
  <div>
    <el-table :data="studentList" style="width: 100%" border>
      <el-table-column prop="studentCode" label="学号"></el-table-column>
      <el-table-column prop="studentName" label="姓名"></el-table-column>
      <el-table-column prop="courseCode" label="课程编号"></el-table-column>
      <el-table-column prop="courseName" label="课程名"></el-table-column>
      <el-table-column prop="achievement" label="成绩"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="{}">
          <el-input
            v-model="searchCode"
            size="mini"
            placeholder="根据学号查询学生所选课程"
            @keydown.native.enter="searchCodeMethod"
          />
          <el-input
            v-model="searchName"
            size="mini"
            placeholder="根据课程名称查询选修该课程的学生"
            @keydown.native.enter="searchNameMethod"
          />
        </template>
      </el-table-column>
    </el-table>
    <div class="jkjk">平均分：{{ avg }}</div>
    <div class="jkjk">最高分：{{ maxAchievement }}</div>
    <div class="jkjk">最低分：{{ minAchievement }}</div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'XuanKe',
  data() {
    return {
      searchCode: '',
      searchName: '',
      studentList: [],
      avg: '',
      maxAchievement: '',
      minAchievement: '',
    }
  },
  mounted() {},
  methods: {
    searchCodeMethod() {
      axios
        .get(`/scms/student-course/select-id?code=${this.searchCode}`)
        .then(res => {
          this.studentList = res.data
        })
        .catch(err => {
          console.error(err)
        })
      axios
        .get(`/scms/student-course/select-avg-achievement?code=${this.searchCode}`)
        .then(res => {
          this.avg = res.data[0].achievement
        })
        .catch(err => {
          console.error(err)
        })
    },
    searchNameMethod() {
      axios
        .get(`/scms/student-course/select-course-student`, {
          params: {
            page: 0,
            name: this.searchName,
          },
        })
        .then(res => {
          this.studentList = []
          res.data.content.forEach(element => {
            this.studentList.push(element)
          })
        })
        .catch(err => {
          console.error(err)
        })

      axios
        .get(`/scms/student-course/select-min-max-avg`, {
          params: {
            name: this.searchName,
          },
        })
        .then(res => {
          this.avg = res.data[0].avgAchievement
          this.maxAchievement = res.data[0].maxAchievement
          this.minAchievement = res.data[0].minAchievement
        })
        .catch(err => {
          console.error(err)
        })
    },
  },
}
</script>

<style scoped>
.jkjk {
  margin-top: 100px;
}
</style>
