<template>
  <div>
    <el-table :data="studentList" style="width: 100%" border>
      <el-table-column prop="code" label="学号"></el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="sex" label="性别"></el-table-column>
      <el-table-column prop="birthday" label="出生日期"></el-table-column>
      <el-table-column prop="email" label="邮箱"></el-table-column>
      <el-table-column prop="phone" label="电话"></el-table-column>
      <el-table-column prop="department" label="系部"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="{}">
          <el-input
            v-model="searchId"
            size="mini"
            placeholder="根据ID搜索"
            @keydown.native.enter="searchIdMethod"
          />
          <el-input
            v-model="searchCode"
            size="mini"
            placeholder="根据Code搜索"
            @keydown.native.enter="searchCodeMethod"
          />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">
            Delete
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="学生信息修改"
      :visible.sync="dialogVisible"
      width="30%"
      :showClose="false"
      top="40px"
    >
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="学号">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-select v-model="form.sex" placeholder="请选择性别">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="birthday1"
              style="width: 100%"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-time-picker
              placeholder="选择时间"
              v-model="birthday2"
              style="width: 100%"
              value-format="HH-mm-ss"
            ></el-time-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="系部">
          <el-input v-model="form.department"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="okBtn()">确 定</el-button>
      </span>
    </el-dialog>
    <el-pagination
      @current-change="handleCurrentChange"
      background
      layout="prev, pager, next"
      :page-count="totalPages"
      class="bottom"
    ></el-pagination>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'StudentCRUD',
  data() {
    return {
      studentList: [],
      dialogVisible: false,
      current: 0,
      totalPages: 0,
      searchId: '',
      searchCode: '',
      birthday1: '',
      birthday2: '',
      form: {
        id: '',
        code: '',
        name: '',
        sex: '',
        birthday: '',
        email: '',
        phone: '',
        department: '',
      },
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.current = val - 1
      this.getStudent()
    },
    handleEdit(_, row) {
      this.dialogVisible = true
      this.form.id = row.id
      this.form = row
    },
    handleDelete(_, row) {
      axios
        .post(`/scms/student/delete?id=${row.id}`)
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
          this.getStudent()
        })
        .catch(err => {
          console.error(err)
        })
    },
    okBtn() {
      this.dialogVisible = false
      this.$set(this.form, 'birthday', this.birthday1 + ' ' + this.birthday2)
      axios
        .post(`/scms/student/update`, this.form)
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
          this.getStudent()
        })
        .catch(err => {
          console.error(err)
        })
    },
    getStudent() {
      axios
        .get(`/scms/student/list?page=${this.current}`)
        .then(res => {
          this.studentList = res.data.content
          this.totalPages = res.data.totalPages
          res.data.content.forEach(element => {
            if (element.birthday) {
              const arr = element.birthday.split(' ')
              this.birthday1 = arr[0]
              this.birthday2 = arr[1]
            }
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
    searchIdMethod() {
      if (this.searchId === '') {
        this.getStudent()
        return
      }
      axios
        .get(`/scms/student/id-list?id=${this.searchId}`)
        .then(res => {
          this.studentList = []
          this.studentList.push(res.data)
          this.$message({
            message: 'search id successfully',
            type: 'success',
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
    searchCodeMethod() {
      if (this.searchCode === '') {
        this.getStudent()
        return
      }
      axios
        .get(`/scms/student/code-list?code=${this.searchCode}`)
        .then(res => {
          this.studentList = []
          this.studentList.push(res.data)
          console.log(res.data)
          this.$message({
            message: 'search code successfully',
            type: 'success',
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
  },
  mounted() {
    this.getStudent()
  },
}
</script>
<style>
.el-main[data-v-42f7020c] {
  line-height: 0px;
}
</style>
<style scoped>
.bottom {
  position: absolute;
  top: 90%;
  left: 72%;
}
</style>
