<template>
  <div>
    <div class="sc">
      <el-form ref="form" :model="form" label-width="50px">
        <el-form-item label="学号">
          <el-input v-model="studentCode"></el-input>
        </el-form-item>
        <el-form-item label="课程">
          <el-input v-model="courseCode"></el-input>
        </el-form-item>
        <el-form-item label="成绩">
          <el-input v-model="form.achievement"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="okBtn()">选 课</el-button>
      <el-button type="primary" @click="exitBtn()">退 选</el-button>
      <el-button type="primary" @click="evaluateBtn()">成绩评定</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'StudentCourse',
  data() {
    return {
      studentCode: '',
      courseCode: '',
      form: {
        achievement: '',
      },
    }
  },
  methods: {
    okBtn() {
      axios
        .post(
          `/scms/student-course/add?studentCode=${this.studentCode}&courseCode=${this.courseCode}`
        )
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
    exitBtn() {
      axios
        .get(`/scms/student-course/delete`, {
          params: {
            studentCode: this.studentCode,
            courseCode: this.courseCode,
          },
        })
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
    evaluateBtn() {
      axios
        .get('/scms/student-course/update', {
          params: {
            studentCode: this.studentCode,
            courseCode: this.courseCode,
            achievement: this.form.achievement,
          },
        })
        .then(res => {
          this.$message({
            message: res.data,
            type: 'success',
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
  },
}
</script>

<style scoped>
.sc {
  height: 260px;
  width: 400px;
  background-color: aliceblue;
  margin-top: 10px;
  padding: 10px 10px;
}
</style>
