import VueRouter from 'vue-router'

import SignUp from '../pages/SignUp'
import UserShow from '../pages/UserShow'

import StudentCRUD from '../pages/StudentCRUD'
import CourseCRUD from '../pages/CourseCRUD'
import TeacherCRUD from '../pages/TeacherCRUD'
import StudentCourse from '../pages/StudentCourse'
import XuanKe from '../pages/XuanKe'

// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/signup',
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp,
      meta: { title: '登录' },
    },
    {
      path: '/home',
      name: 'UserShow',
      component: UserShow,
      redirect: '/home/student',
      children: [
        {
          path: 'student',
          name: 'StudentCRUD',
          component: StudentCRUD,
          meta: { title: '学生管理' },
        },
        {
          path: 'course',
          name: 'CourseCRUD',
          component: CourseCRUD,
          meta: { title: '课程管理' },
        },
        {
          path: 'teacher',
          name: 'TeacherCRUD',
          component: TeacherCRUD,
          meta: { title: '教师管理' },
        },
        {
          path: 'studentcourse',
          name: 'StudentCourse',
          component: StudentCourse,
          meta: { title: '学生选课' },
        },
        {
          path: 'xuanke',
          name: 'XuanKe',
          component: XuanKe,
          meta: { title: '选课查看' },
        },
      ],
    },
  ],
})

// 为每个页面加上title
router.afterEach(to => {
  document.title = to.meta.title
})

export default router
